import { Button, Col, Form, Input, Row } from "antd";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import DefaultLayout from "../components/DefaultLayout";
import Spinner from "../components/Spinner";
import { editCar, getAllCars } from "../redux/actions/carsActions";

function EditCar() {
  const dispatch = useDispatch();
  const { cars } = useSelector((state) => state.carsReducer);
  const { loading } = useSelector((state) => state.alertsReducer);
  const [car, setCar] = useState(null); // Initialize car state as null
  const { carid } = useParams();

  useEffect(() => {
    if (cars.length === 0) {
      dispatch(getAllCars());
    } else {
      const selectedCar = cars.find((o) => o._id === carid);
      if (selectedCar) {
        setCar(selectedCar);
      }
    }
  }, [cars, carid, dispatch]);

  const onFinish = (values) => {
    values._id = car._id; // Ensure the car's ID is sent with the form data
    dispatch(editCar(values)); // Dispatch the editCar action with the form values
    console.log("Success:", values);
  };

  if (loading) {
    return <Spinner />;
  }

  if (!car) {
    return <div>Loading...</div>;
  }

  return (
    <DefaultLayout>
      <Row justify="center" className="mt-5">
        <Col lg={12} sm={24}>
          <Form
            initialValues={car}
            className="p-2"
            layout="vertical"
            onFinish={onFinish}
          >
            <h1>Edit Car</h1>
            <h5>{car.name}</h5>
            <Form.Item
              label="Car Name"
              name="name"
              rules={[
                {
                  required: true,
                  message: "Please input the car name!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Image URL"
              name="image"
              rules={[
                {
                  required: true,
                  message: "Please input the image URL!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Price per Day"
              name="rentPerHour"
              rules={[
                {
                  required: true,
                  message: "Please input the price per day!",
                },
              ]}
            >
              <Input type="number" />
            </Form.Item>

            <Form.Item
              label="Capacity"
              name="capacity"
              rules={[
                {
                  required: true,
                  message: "Please input the capacity!",
                },
              ]}
            >
              <Input type="number" />
            </Form.Item>

            <Form.Item
              label="Fuel Type"
              name="fuelType"
              rules={[
                {
                  required: true,
                  message: "Please input the fuel type!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item>
              <div className="text-right">
                <Button type="primary" htmlType="submit">
                  Edit Car
                </Button>
              </div>
            </Form.Item>
          </Form>
        </Col>
      </Row>
    </DefaultLayout>
  );
}

export default EditCar;
