import React, { useEffect, useState } from "react";
import DefaultLayout from "../components/DefaultLayout";
import { useDispatch, useSelector } from "react-redux";
import { getAllCars } from "../redux/actions/carsActions";
import { Col, DatePicker, Row, Card, Button } from "antd";
import Spinner from "../components/Spinner";
import { Link } from "react-router-dom";
const { Meta } = Card;

function Home() {
  const { cars } = useSelector((state) => state.carsReducer);
  const { loading } = useSelector((state) => state.alertsReducer);
  const [totalCars, setTotalCars] = useState([]);
  const { RangePicker } = DatePicker;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllCars());
  }, [dispatch]);

  useEffect(() => {
    setTotalCars(cars); // Set the total cars when the car data is fetched
  }, [cars]);

  const setFilter = (dates) => {
    if (!dates) {
      setTotalCars(cars); // If no filter is applied, show all cars
    } else {
      const [startDate, endDate] = dates;
      const filteredCars = cars.filter((car) => {
        // Add your filtering logic here based on the car's available date range
        // For example, check if the car is available within the selected date range.
        return (
          new Date(car.availableFrom) <= endDate &&
          new Date(car.availableTo) >= startDate
        );
      });
      setTotalCars(filteredCars); // Update the displayed cars based on the date range
    }
  };

  return (
    <DefaultLayout>
      <Row className="mt-3" justify="center">
        <Col lg={20} sm={24} className="d-flex justify-content-flex">
          <RangePicker onChange={setFilter} />
        </Col>
      </Row>

      {loading === true && <Spinner />}

      <Row justify="center">
        {totalCars.length > 0 ? (
          totalCars.map((car) => {
            return (
              <Col
                lg={5}
                sm={24}
                xs={24}
                style={{ padding: 5, margin: 5 }}
                key={car._id}
              >
                <Card
                  hoverable
                  style={{
                    width: 240,
                  }}
                  cover={
                    <img alt="example" src={car.image} className="card-img" />
                  }
                >
                  <Meta
                    title={car.name}
                    description={`$${car.rentPerHour} per hour`}
                  />
                  <div style={{ padding: 5, marginTop: 10 }}>
                    <Button type="primary">
                      <Link to={`/booking/${car._id}`}>Book now</Link>
                    </Button>
                  </div>
                </Card>
              </Col>
            );
          })
        ) : (
          <Col span={24}>
            <h3>No cars available for the selected date range</h3>
          </Col>
        )}
      </Row>
    </DefaultLayout>
  );
}

export default Home;
